import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { InsertBook } from '../SQLite/Books';
import { useSQLite } from 'react-sqlite-hook';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 300,
    height: 500,
    bgcolor: 'background.paper',
    borderRadius: '12px',
    boxShadow: 24,
    p: 4,
    overflow: 'scroll !important'
};

export const BasicModal = ({ openModal, setOpenModal, src, title, authors, description }) => {
    const [isAddingBook, setIsAddingBook] = useState(false);
    const { db, initDb } = useSQLite({
        dbName: 'libraryDB',
        version: '1.0'
    });

    const handleAdd = async () => {
        try {
            if (db) {
                console.log('entro en el handleInsert');
                await db.executeSql(
                    'INSERT INTO books (bookName, author, description, imgSource) VALUES (?, ?, ?, ?)',
                    [title, authors, description, src]
                );
                console.log('termino el handleInsert');
            } else {
                console.log('no existe la base de datos. Se procede a crearla');
                await initializeDatabase(); // Esperar a que se complete la inicializacin de la base de datos
            }
        } catch (error) {
            console.error('Error al insertar el libro:', error);
        }
    };

    const initializeDatabase = async () => {
        try {
            console.log('Creando la base de datos...');
            await initDb(); // Inicializa la base de datos si no existe
            console.log('Base de datos creada.');
        } catch (error) {
            console.error('Error al crear la base de datos:', error);
        }
    };

    return (
        <div>
            <Modal
                open={openModal}
                onClose={() => setOpenModal(!openModal)}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <div className="card">
                        <img src={src} />
                        <div className="bottom">
                            <Button color="error" variant="contained" onClick={handleAdd}>
                                Agregar a biblioteca
                            </Button>
                            <h3 className="cardTitle">{title}</h3>
                            <p className="cardAuthors">{authors}</p>
                            <p className="cardDescription">{description}</p>
                        </div>
                    </div>
                </Box>
            </Modal>
        </div>
    );
};
